//

import React from 'react'
import {observable, action} from 'mobx'
import {observer} from 'mobx-react'
// import Cat from './components/Cat'
import utils from './utils'

class Store {
    @observable loading = true
    @observable result = {
        url: '',
        limit: 12,
        offset: 0,
        total: 0,
        kitties: [],
    }
    @action setState = (state) => {
        for (var k in state) {
            this[k] = state[k]
        }
    }

    @action loadKitties(address) {
        return fetch(`https://api.cryptokitties.co/kitties?offset=0&limit=12&parents=false&authenticated=true&owner_wallet_address=${address}`)
            .then(res => res.json())
            .then(res => {
                this.setState({loading: false, result: res})
            })
    }
}

const store = new Store()

@observer
export default class MyKitties extends React.Component {

    renderNotKitty() {
        return <div>
            <h2 className="Hero-h2 u-margin-top-lg">您还没有猫咪。</h2>
            {/*<p className="Hero-description">这里有一些猫咪正在寻找一个新的家。</p>*/}
        </div>
    }

    componentDidMount() {
        // store.loadKitties('0x0dce6afc35907a5bca6e6db442c4573755de4566')
        store.loadKitties(window.web3.eth.defaultAccount)
    }

    renderLoading() {
        return <div className="Loader"><span className="Loader-img"></span></div>
    }

    renderKitties(kitties) {
        return <div>
            <div className="KittiesGrid">
                {kitties.map(row => <Cat kitty={row} key={row.id}/>)}
            </div>
        </div>
    }

    render() {
        var {loading, result} = store
        var {kitties} = result
        return <div className="App">
            <main className="Main">
                <div className="ProfilePage">
                    <div className="ProfileFilterBar">
                        <div className="Filter Filter--purple">
                            <div className="Filter-container">
                                <div className="Filter-tabs" style={{margin: '0'}}><span className="Filter-tab Filter-tab--active">所有猫咪</span></div>
                            </div>
                        </div>
                    </div>
                    <div className="ProfileNoKitties--mine">
                        <div className="Hero">
                            <div className="Container Container--sm Container--center">
                                {loading && this.renderLoading()}
                                {!loading && (!kitties || kitties.length === 0) && this.renderNotKitty()}
                                {!loading && kitties && this.renderKitties(kitties)}
                            </div>
                        </div>
                    </div>
                </div>
            </main>
        </div>
    }
}


class Cat extends React.Component {
    render() {
        var {kitty} = this.props
        var price = kitty.auction.current_price ? utils.toFixed(window.web3.fromWei(kitty.auction.current_price), 4) : null
        var cooldown = utils.getCooldown(kitty.status.cooldown_index)
        return <div className="KittiesGrid-item"><a aria-current="false" to={`/kitty/${kitty.id}`}>
            <div className="KittyCard-wrapper">
                <div
                    className={`KittyCard u-bg-alt-${kitty.color} KittyCard--responsive KittyCard--shadow-${kitty.color}`}>
                    <img className="KittyCard-image" src={kitty.image_url_cdn} alt="Kitty #528739 - null"/>
                    <div className="KittyCard-status">
                        {price !== null &&
                        <div className="KittyStatus">
                            <div className="KittyStatus-item">
                                <span className="KittyStatus-itemIcon"><i className="Icon Icon--tag"/></span>
                                <span className="KittyStatus-itemText">
                                    <span className="KittyStatus-note"><small>Ξ</small>
                                        {price}</span>
                                </span>
                            </div>
                        </div>
                        }
                    </div>
                    {kitty.generation === 0 &&
                    <span className="KittyCard-newBadge"><svg className="NewBadge" width="100%" height="100%"
                                                              viewBox="0 0 38 38"><g fill="none" fillRule="evenodd"><path
                        d="M37.985 0L0 37.985V0h37.985z" fill="#E96BD4"/><text fill="#FFF"
                                                                               fontFamily="Calibre-Semibold, Calibre"
                                                                               fontSize={12} fontWeight={500}
                                                                               letterSpacing=".4"
                                                                               transform="rotate(-45 13.581 14.581)"><tspan
                        x="3.081" y="16.581">全新</tspan></text></g></svg></span>
                    }
                </div>
                <div className="KittyCard-details"><span
                    className="KittyCard-details-item KittyCard-details-item--highlight">Kitty {kitty.id}</span><span
                    className="KittyCard-details-item">代 {kitty.generation}</span><span
                    className="KittyCard-details-item">{cooldown}</span></div>

            </div>
        </a>
        </div>
    }
}


