
function toFixed(num, dec){
    return num.substring(0 , num.lastIndexOf('.') + dec + 1);
}

const cooldown = [
    "Fast",        // 0
    "Swift",       // 1,2
    "Snappy",      // 3,4
    "Brisk",       // 5,6
    "Plodding",    // 7,8
    "Slow",        // 9,10
    "Sluggish",    // 11,12
    "Catatonic",   // 13,14
]

function getCooldown(index) {
    if (index > 12) {
        return cooldown[7]
    } else if (index > 10) {
        return cooldown[6]
    } else if (index > 8) {
        return cooldown[5]
    } else if (index > 6) {
        return cooldown[4]
    } else if (index > 4) {
        return cooldown[3]
    } else if (index > 2) {
        return cooldown[2]
    } else if (index > 0) {
        return cooldown[1]
    } else {
        return cooldown[0]
    }
}

export default {
    toFixed,
    getCooldown,
}


